.terminal-bg {
  background: linear-gradient(180deg, #020617 0%, #0f172a 100%);
}

.terminal-glow {
  box-shadow: 0 0 20px rgba(249, 115, 22, 0.1);
}

.hard-shadow {
  box-shadow: 4px 4px 0px 0px rgba(0,0,0,0.1);
}

.hard-shadow:hover {
  transform: translateY(-2px);
  box-shadow: 6px 6px 0px 0px rgba(0,0,0,0.1);
}