@import url('https://fonts.googleapis.com/css2?family=Chivo:wght@700;900&family=Manrope:wght@400;500;600&family=JetBrains+Mono:wght@400;500&display=swap');

@tailwind base;
@tailwind components;
@tailwind utilities;

@layer base {
  :root {
    --background: 248 250 252;
    --foreground: 15 23 42;
    --card: 255 255 255;
    --card-foreground: 15 23 42;
    --popover: 255 255 255;
    --popover-foreground: 15 23 42;
    --primary: 15 23 42;
    --primary-foreground: 248 250 252;
    --secondary: 100 116 139;
    --secondary-foreground: 248 250 252;
    --muted: 241 245 249;
    --muted-foreground: 100 116 139;
    --accent: 249 115 22;
    --accent-foreground: 255 255 255;
    --destructive: 239 68 68;
    --destructive-foreground: 255 255 255;
    --border: 226 232 240;
    --input: 226 232 240;
    --ring: 15 23 42;
    --radius: 0.25rem;
  }

  .dark {
    --background: 2 6 23;
    --foreground: 248 250 252;
    --card: 15 23 42;
    --card-foreground: 248 250 252;
    --popover: 15 23 42;
    --popover-foreground: 248 250 252;
    --primary: 248 250 252;
    --primary-foreground: 15 23 42;
    --secondary: 71 85 105;
    --secondary-foreground: 248 250 252;
    --muted: 30 41 59;
    --muted-foreground: 148 163 184;
    --accent: 249 115 22;
    --accent-foreground: 255 255 255;
    --destructive: 239 68 68;
    --destructive-foreground: 255 255 255;
    --border: 30 41 59;
    --input: 30 41 59;
    --ring: 248 250 252;
  }
}

@layer base {
  * {
    @apply border-border;
  }
  body {
    @apply bg-background text-foreground;
    font-family: 'Manrope', sans-serif;
  }
  h1, h2, h3, h4, h5, h6 {
    font-family: 'Chivo', sans-serif;
  }
  code, pre {
    font-family: 'JetBrains Mono', monospace;
  }
}